.inputStyle {
    width: 4em;
    padding: .8em;
    border-radius: 5px;
    border: 1px solid #fff;
    background: transparent;
    color: #fff;
    overflow: hidden;
    font-size: 1.2em;
}

.PlayPauseButton {
    background-color: #a577d2;
    border: none;
    width: 3.5em;
    height: 3.5em;
    border-radius: 5px;
}

.PlayPauseButton:hover {
    background-color: #6d33a7;
    border: none;
}

.PlayPauseButton i {
    color: #ffc0cb;
}

.Clock {
    width: 50%;
    margin-top: 2.8em;
}


@media (max-width: 800px) {
    .inputStyle {
        width: 4em;
        padding: .8em;
        font-size: 1em;
    }
    
    .PlayPauseButton {
        width: 2.5em;
        height: 2.5em;
    }

    .PlayPauseButton i {
        padding: 0px;
        font-size: 1.4em;
    }
}

@media (max-width: 767px) {
    .Clock {
        width: 25%;
        margin-top: 2.8em;
    }
}